import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { withRouter } from "react-router";
import FileUploader from "Component/FileUploader";
import ReactExport from "react-export-excel";
import { getWarehouses } from "apiServices";
import "./index.scss";

const ExcelFile = ReactExport.ExcelFile;
const ExcelSheet = ReactExport.ExcelFile.ExcelSheet;
const ExcelColumn = ReactExport.ExcelFile.ExcelColumn;
const Upload = (props) => {
  const dispatch = useDispatch();
  const warehousesData = useSelector((state) => state.warehouseData);
  const Field = [
    {
      dataField: "id", //field ini wajib ada untuk editable column
      text: "ID",
      editable: false,
      sort: true,
      hidden: true,
      type: "number",
    },
    {
      dataField: "wmsPoSkuCode",
      text: "PO SKU Code",
      type: "number",
      sort: true,
    },
    {
      dataField: "skuNumber",
      text: "SKU Number",
      type: "number",
      sort: true,
    },
    {
      dataField: "expiryDateCol",
      text: "Expiry Date",
      type: "date_indo",
      sort: true,
    },
    {
      dataField: "warehouse",
      text: "Warehouse Source",
      type: "string",
      sort: true,
    },
    { 
      dataField: "areaName", 
      text: "Area Source", 
      type: "string", 
      sort: true 
    },
    {
      dataField: "warehouseDestination",
      text: "Warehouse Destination",
      type: "string",
      sort: true,
    }, 
    { 
      dataField: "qty", 
      text: "Input Weight", 
      type: "number", 
      sort: true 
    },
  ];

  const dataSet1 = [
    {
      areaSource: "",
      warehouseSource: "",
      warehouseDestination: "",
      expiryDate: "",
      po: "",
      skuNumber: "",
      inputWeight: "",
    },
  ];

  useEffect(() => {
    if (!warehousesData) {
      getWarehouses({ dispatch });
    }
  }, []);

  return (
    <div className="mb-5">
      {/* export this table to excel */}
      <ExcelFile
        element={
          //dont change id and style please
          <button id="download-template-xls" style={{ display: "none" }}>
            Download Data
          </button>
        }
        //file name without types
        filename="StockTransferUpload_Template"
      >
        <ExcelSheet data={dataSet1} name="template">
          <ExcelColumn label="PO SKU Code" value="po" />
          <ExcelColumn label="SKU Number" value="skuNumber" />
          <ExcelColumn label="Expiry Date" value="expiryDate" />
          <ExcelColumn label="Warehouse Source" value="warehouseSource" />
          <ExcelColumn label="Area Source" value="areaSource" />
          <ExcelColumn label="Warehouse Destination" value="warehouseDestination" />
          <ExcelColumn label="Input Weight" value="inputWeight" />
        </ExcelSheet>
      </ExcelFile>
      {/* end export this table to excel */}

      <FileUploader
        nextPage={() =>
          props.history.push(
            "/inventory-entry/stock-transfer/CSV-upload/review"
          )
        }
        transaction={"stockTransfer"}
        field={Field}
        type="stockTransfer"
      />
    </div>
  );
};

export default withRouter(Upload);
